<template>
  <div ref="container">
    <div ref="left" class="map"></div>
    <div v-if="config.leftLabel" class="label--left">
      {{ config.leftLabel }}
    </div>
    <div ref="right" class="map"></div>
    <div v-if="config.rightLabel" class="label--right">
      {{ config.rightLabel }}
    </div>
  </div>
</template>

<script>
import MapboxGlCompare from 'mapbox-gl-compare';

export default {
  props: {
    config: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      leftMap: null,
      rightMap: null,
      map: null,
      mapboxglCompare: null,
      popup: null,
    };
  },
  mounted() {
    // You can load mapbox as a Vue plugin or through a global script, depending on where you plan to run this component.
    this.mapboxgl = this.$mapboxgl || window.mapboxgl;
    // Mapbox Gl Compare will attach itself to the window mapboxgl instance if it exists. Otherwise, it will export a module
    // So we need to capture that behavior as well.
    if (window.mapboxgl) {
      this.mapboxglCompare = window.mapboxgl.Compare;
    } else {
      this.mapboxglCompare = MapboxGlCompare;
    }
    this.leftMap = new this.mapboxgl.Map({
      ...this.config.leftMapConfig.mapProperties,
      container: this.$refs.left,
      style: this.config.style,
    });
    this.rightMap = new this.mapboxgl.Map({
      ...this.config.rightMapConfig.mapProperties,
      container: this.$refs.right,
      style: this.config.style,
    });
    this.leftMap.on('load', () => {
      this.config.leftMapConfig.layers.map((layer) => {
        this.leftMap.setLayoutProperty(
          layer.name,
          'visibility',
          layer.visibility
        );
      });
    });
    this.rightMap.on('load', () => {
      this.config.rightMapConfig.layers.map((layer) => {
        this.rightMap.setLayoutProperty(
          layer.name,
          'visibility',
          layer.visibility
        );
      });
    });
    this.map = new this.mapboxglCompare(
      this.leftMap,
      this.rightMap,
      this.$refs.container
    );
    window.GLOBAL_MAP_VAR = this.map;
  },
};
</script>

